/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-input size="small" v-model="formInline.search" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
      <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" id="ou" :data="listData" highlight-current-row v-loading="loading" border
      element-loading-text=""
      :row-class-name="tableRowClassName"
      @selection-change="handleSelectionChange" style="width: 100%;">
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="name" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="sex" label="" width="70">
      </el-table-column>
      <el-table-column sortable prop="createdAt" label="" width="150">
        <template slot-scope="scope">
          <div>{{scope.row.createdAt|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column  label="" width="120">
        <template slot-scope="scope">
          <el-button @click="openCalendar(scope.row)" size="mini" type="primary" style="margin-left: 16px;">
          </el-button>
        </template>
      </el-table-column>
      <el-table-column  label=""  width="280">
        <template slot-scope="scope">
          <el-button type="text"  @click="openSetTeachingVideo(scope.$index, scope.row)" size="mini" ></el-button>
        </template>
      </el-table-column>
      <el-table-column  label=""  width="280">
        <template slot-scope="scope">
          <el-button type="text"  @click="editRemark(scope.row)" size="mini" ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>

    <el-drawer ref="refdrawer" :visible.sync="drawer" size="35%" :with-header="false">
      <span style="color:red;margin:15px 15px"></span>
      <el-calendar>
        <!--  2.5 slot  2.6 slot  data.day yyyy-MM-dd-->
        <template slot="dateCell" slot-scope="{date, data}">
          <p :class="data.isSelected ? 'is-selected' : ''">
            {{ data.day.split('-').slice(1).join('-') }}
          </p>
          <div v-for="(item,index) in marks" :key="index" style="width:0px;height:0px">
            <el-tag v-if="data.day == item.date" size="mini" :disable-transitions="false"
              @click="handleEdit('',{todayClockInId:item.id,lastCheckInVideos:item.videos})">
            </el-tag>
          </div>
        </template>
      </el-calendar>
    </el-drawer>
    <!--  -->
    <el-dialog :title="videoTitle" :visible.sync="editFormVisible" width="30%" @click="closeVideoDialog"
      @open="openVideoDialog" append-to-body>
      <div>
        <video ref="videoPlay" width="100%" height="100%" :src="viewVideoUrl" autoPlay preload="auto" controls>
        </video>
      </div>
      <div>
        <el-button-group>
          <el-button v-for="btn in currentDayVideos" :key="btn.id" type="danger" @click="changeVideo(btn.id)" >
           {{btn.name}}
          </el-button>
      </el-button-group>
      </div>
      <div>
        <el-form :model="editForm">
          <el-form-item label="" label-width="80">
            <el-input v-model="editForm.replyContent" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')">
        </el-button>
        <el-button size="small" @click="closeVideoDialog"></el-button>
      </div>
    </el-dialog>
     <!--  -->
     <el-dialog title="" :visible.sync="settingFormVisible" width="30%" @click="closeDialog" append-to-body>
      <el-timeline reverse="true">
        <el-timeline-item v-for="(activity, index) in activities" :key="index" :timestamp="activity.timestamp">
          {{activity.content}}
        </el-timeline-item>
      </el-timeline>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { PatientRecordsList, GetVideoLinkById, GetVideoesList,PatientsDelete,
  GetClockInByOpenId, GetPatientSetting, SetPatientVideo,
  Patientfeedback } from '../../api/basisMg'
import Pagination from '../../components/Pagination'

export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      settingFormVisible:false,//
      settingVideo:{pid:'',tid:''},
      settingVideoOptions:[],
      viewVideoUrl:'',
      replyFormVisible:false,
      videoTitle:'',
      title: '',
      drawer: false,
      todayClockInId:'',//id
      marks:[],
      editForm: {
        replyContent: ''
      },
      currentDayVideos:[],
      activities: [],
      form:{},
      daterange:'',
      // rules
      rules: {
        deptName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        deptNo: [{ required: true, message: '', trigger: 'blur' }]
      },
      formInline: {
        pageIndex: 1,
        pageSize: 10,
        search: '',
        token: localStorage.getItem('logintoken')
      },
      multipleSelection:[],
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true
      /***
       *  
       */
       PatientRecordsList(parameter)
        .then(res => {
          this.loading = false
          if (res.data.code <0) {
            this.$message({
              type: 'info',
              message: res.data.message
            })
          } else {
            this.listData = res.data.data.list
            // 
            this.pageparm.currentPage = this.formInline.pageIndex
            this.pageparm.pageSize = this.formInline.pageSize
            this.pageparm.total = res.data.data.count
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.pageIndex = parm.currentPage
      this.formInline.pageSize = parm.pageSize
      this.getdata(this.formInline)
    },
    //
    openSetTeachingVideo: function (index, row) {
      this.settingVideo.pid = row.id
      this.settingFormVisible = true
      GetPatientSetting(this.settingVideo.pid)
      .then(res => {
          if (res.data.code > 0) {
            this.activities = res.data.data.activities
            this.settingVideo.tid = res.data.data.setTeachVideos.map(o=>o.id)
          } else {
            this.$message({
              type: 'info',
              message: res.data.message
            })
          }
        })
      .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    search() {
      this.getdata(this.formInline)
    },

    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //
    editRemark(row){
      this.$alert(':'+row.remark, '', {
          confirmButtonText: '',
          callback: action => {
            this.$message({
              type: 'info',
              message: `ok`
            });
          }
        });
    },

    // 
    closeDialog() {
      this.loading = false
      this.editFormVisible = false
      this.replyFormVisible = false
      this.settingFormVisible =false
    },
    // 
    closeVideoDialog(){
      this.$refs.videoPlay.pause();
      this.closeDialog();
    },
    openVideoDialog(){

    },
    openCalendar(row){
      this.drawer = true
      //
      GetClockInByOpenId(row.openId).then(res => {
        if (res.data.code > 0) {
         this.marks = res.data.data
        } else {
          this.$message({
            type: 'info',
            message: res.data.message
          })
        }
      }).catch(err => {
        this.$message.error(err);
      });


    },
    fermitTime(time) {
      var now = new Date(time);
      var year = now.getFullYear();
      var mon = now.getMonth() + 1;
      var date = now.getDate();
      if (mon < 10) {
        mon = '0'+mon;
      };
      if (date < 10) {
        date = '0'+date;
      }
      var postDate = year +'-'+mon+'-'+date;
      return postDate;
    },
    tableRowClassName({ row, rowIndex }) {

      if (row.sameDayIsCheck == '') {
        return 'success-row';
      }
      return '';
    }
  }
}
</script>

<style>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.el-table .warning-row {
    background: oldlace;
}
.el-table .success-row {
    background: #cff3bb;
}
</style>





